/**
 * Erro customizado para o tipo de usurio
 * @class UserTypeException
 * @extends {Error}
 * @param {string} message Mensagem de erro
 * @param {number} statusCode Cdigo de status HTTP
 * @example
 * throw new UserTypeException("No foi possvel identificar o tipo de usurio", 500);
 */
export class HTMLElementException extends Error {
    public statusCode: number;
  
    constructor(message: string, statusCode: number) {
      super(message);
  
      this.name = this.constructor.name;
  
      this.statusCode = statusCode;
    }
  }
  